import Logo from '~/components/Logo.vue'
import Navbar from "~/components/navbar";

export default {
  name: "home",
  data: () => ({
    wishes: [],
    connected: false,
    notConnected: true
  }),
  components: {Navbar},
  methods: {
    async getWishes() {
      const wishes = await this.$axios.$get('/api/wish')
      this.wishes = wishes
    }
  },
  created() {
    if (process.browser) {
      if (localStorage.getItem('jwt') != null) {
        this.connected = true
        this.notConnected = false
        this.getWishes()
      }
    }
  }
}
